

.features__heading {
    grid-column:1/-1;
    justify-self: center;
}
.feature {
    display:grid;
    grid-template-columns: min-content 1fr;
    grid-row-gap: 1.5rem;
    grid-column-gap: 2.5rem;


    &__icon {
        fill: $color-primary;
        width:4.5rem;
        height: 4.5rem;
        grid-row: 1/ span 2;
        transform: translateY(-1rem);
    }
    &__text {
        font-size: 1.7rem;
        
    }
    &__heading {
        text-transform: uppercase;
    }
}
